import { BpmnModel } from "./BpmnModel";
import { ThrowEvent } from "./ThrowEvent";

export class EndEvent extends ThrowEvent {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.EndEvent";

    NewInstance(): EndEvent {
        return new EndEvent(this.Model);
    }
}